<template>
    <div class="about">
        <div style="display: flex;justify-content: space-around;" class="events">

            <div @click="setValue(index)" class="every" :style="'background: url('+item+'.jpg)'" :key="index"
                 v-for="(item,index) in 6">
                <div>></div>
                <p>{{texts[index]}}</p>
            </div>
        </div>
        <vue-slider
                v-model="value"
                :tooltip="'none'"
                :interval="10"
                :max="120"
                ref="slider"
                :lazy="true"
                :process-style="{ backgroundColor: 'rgba(38,56,140)' }"
                :tooltip-style="{ backgroundColor: 'pink', borderColor: 'pink' }"
        >
            <template v-slot:dot="{ value, focus }">
                <div :class="['custom-dot', { focus }]">
                    <div class="content"></div>
                </div>
            </template>
        </vue-slider>
        <div style="margin-top: 100px">
            <p style="color:rgb(39, 58, 136);margin-bottom: 10px;font-weight: bolder">CLICK THE ONES YOU WANT TO
                OBSERVER</p>
            <div style="display: flex;justify-content: space-around">
                <transition-group name="fade" class="img_list">
                    <img @click="show((valueCHange[value])+'/a'+(index+1)+'.mov')" v-for="(item,index) in 21"
                         :key="index+'_'"
                         :src="(valueCHange[value])+'/a'+(index+1)+'.PNG'"
                         alt="">
                </transition-group>
            </div>
        </div>
        <modal :width="634"
               :height="360" name="hello-world" @before-open="beforeOpen">
            <video
                    :src="src" autoplay="false"></video>
        </modal>
        <BaseTime :TIME_LIMIT="120" @end="go2()" style="position: absolute;bottom:10px;left: 48%"/>
    </div>
</template>
<script>
  // @ is an alias to /src
  import BaseTime from '../components/BaseTime';
  import VueSlider from 'vue-slider-component';
  import 'vue-slider-component/theme/antd.css';

  export default {
    name: 'Home',
    components: {
      VueSlider,
      BaseTime
    },
    data() {
      return {
        value: 0,
        texts: [
          'WANDERING&\rENCOUNTERING',
          'GATHERING',
          'EXERCISING',
          'PROTECTING',
          'STOCKING UP',
          'PATROLLING&\rWORKING'],
        src: '',
        valueCHange: {
          0: 1, 10: 1,
          20: 2,
          30: 2, 40: 3, 50: 3, 60: 4, 70: 4, 80: 5, 90: 5, 100: 6, 110: 6, 120: 6
        },
        selectIndex: 0
      };
    },
    methods: {
      beforeOpen(event) {
        this.src = event.params.src;
      },
      show(e) {
        this.$modal.show('hello-world', {src: e});
      },
      go2() {
        this.$router.push({name: 'Error'});
      },
      hide(e) {
        this.$modal.hide('hello-world', {src: e});
      },
      setValue(index) {
        this.$refs.slider.setValue(index * 10 + 5);
      },
      go() {
        this.$router.push({path: 'page'});
      },
      changeAction(e) {
        this.startTime = e.startTime;
        this.endTime = e.endTime;
      }
    }
  };
</script>
